import React, { Component } from "react";
import classes from "./DownBox.module.scss";

import { useState } from "react";

export function DownBox(props) {
  // props

  const [open, setOpen] = useState(false); // ,   open,   setOpen,    false

  return (
    <div onClick={() => setOpen(!open)} className={classes.DownBox}>
      {" "}
      {/*        */}
      <div className={classes.DownBox__Title}>
        {/*  title  props */}
        <span>{props.title}</span>
        {/*        */}
        {!open ? (
          <i className={"fa fa-chevron-down"} aria-hidden="true"></i>
        ) : (
          <i className={"fa fa-chevron-up"} aria-hidden="true"></i>
        )}
      </div>
      {/*         props */}
      {open && props.title === "" && (
        <div className={classes.DownBox__Content}>
          <p>
            {props.prof}&nbsp; &nbsp;
            {props.work}
          </p>
          <a href={props.workLink} target="_blank">
            {props.workLink}
          </a>
        </div>
      )}
      {open && props.title === "" && (
        <div className={classes.DownBox__Content}>
          {props.tel && <span>:&nbsp;{props.tel}</span>}
          {props.email && <span>Email:&nbsp;{props.email}</span>}
          {props.vk && (
            <a href={props.vk} target="_blank">
              VK
            </a>
          )}
        </div>
      )}
      {/* {open && props.title === "" && (
        <div className={classes.DownBox__Content}>
          {props.look && <span> :&nbsp;{props.look}</span>}
          {props.suggest && <span> :&nbsp;{props.suggest}</span>}
          {props.hobby && <span>:&nbsp;{props.hobby}</span>}
          
        </div>
      )} */}
    </div>
  );
}
